.toggle_link {
	display: table;
	font-size: 14px;
	color: @text-color-arrow;
	text-decoration: none;
	text-align: center;
	margin: 0 auto;
	opacity: 0.4;
	transition: all ease-in-out 0.1s;
	&:hover {
			opacity: 1;
			text-decoration: none;
		}
	span {
		display: block;
		margin-bottom: 15px;
		.font__MyriadPro-Light;
		-webkit-font-smoothing: subpixel-antialiased;
	}
	i {
		display: block;
		margin: 0 auto;
		width: 36px;
		height: 30px;
		background: url("@{path-img}icons-s51a623a2b8.png") 80px -858px;
	}
	&.active {
		span {
			display: none;
		}
		i {
			background: url("@{path-img}icons-s51a623a2b8.png") 40px -858px;
		}
	}
}